.wrapper {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background: #0e1621;
    height: 200px;
    padding-bottom: 50px;
    margin: 0 auto;
}

.wrapperMessage {
    position: relative;
    background: #282c34;
    width: 300px;
    height: auto;
    border-radius: 6px;
    border-bottom-left-radius: 0;
    padding: 11px;
}

.wrapperMessage:after {
    bottom: 0;
    left: -8px;
    position: absolute;
    display: block;
    content: '';
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 9px 9px;
    border-color: transparent transparent #282c34 transparent;


}